import * as React from 'react'
import * as Api from '../api/api'
import IndexLayout from '../layouts'
import {Ez123, MriTypeBreadcrumb} from "../components/breadcrumb";
import {navigate} from "gatsby";
import {SlotAvailabilityTime} from "../api/api";
import {IScan} from "./mri-type";

const take = 4;

interface IState
{
	fname: string, times: Api.SlotAvailabilityDate[] | null, err: any, offset: number;
	lname: string;
	scan: IScan | null;
	haveOrder?: boolean;
	total: number;
}

class PickTimePage extends React.Component<{}, IState> {
	constructor(props, context) {
		super(props, context);
		this.state = {
			fname: '',
			lname: '',
			scan: null,
			times: null,
			err: null,
			offset: 0,
			total: 0,
		};
	}

	public componentDidMount() {
		if (typeof sessionStorage !== 'undefined') {
			const fname = sessionStorage.getItem('fname') || '';
			const lname = sessionStorage.getItem('lname') || '';
			const scan: IScan = JSON.parse(sessionStorage.getItem('scan') || '{}');
			const haveOrder = sessionStorage.getItem('haveOrder') === 'true';
			this.setState({fname, lname, haveOrder, scan}, () => {
				new Api.ScheduleApi().timeSlotsGET({withContrast: scan.contrast === 'with and without contrast', locationId: ''}).then((result) => {
					if (result.value) {
						this.setState({times: result.value || [], total: result.value.length || 0});
					} else {
						this.setState({err: result.message || 'Error'});
					}
				}, (err) => this.setState({err}));
			});
		}
	}

	public renderSlotAvailabilityDate(dt: Api.SlotAvailabilityDate) {
		return (
			<div className="timeslotcolumn">
				<h3>{dt.friendlyBegin}</h3>
				{dt.times ? dt.times.map((timeSlot) => (
					<a key={timeSlot.time}
					   href="#"
					   className={`buttontimeslot ${timeSlot.isAvailable ? '' : 'unavailable '}w-button`}
					   onClick={() => this.pickTime(timeSlot)}
					>
						{timeSlot.time}
					</a>
				)) : <div>dt.times is {typeof dt.times}</div>}
			</div>
		);
	}

	public render() {
		const {offset, total, err, times} = this.state;
		return (
			<IndexLayout>
			<section id="Q2" className="vspace80 w-container">
				<div>
					<Ez123 num={2} />
					<div className="breadcrumb-stack animated zoomIn">
						<MriTypeBreadcrumb value={this.state.scan}/>
					</div>
				</div>
				<div className="w-row">
					<div className="centered w-col w-col-3">
						<img
							src="https://uploads-ssl.webflow.com/5b9e87c40899a487ba8091e4/5b9ead2f3661e73d2f76eedd_Meet%20Our%20Team.svg"
							width={150}
							height={150}
						/>
					</div>
					<div className="w-col w-col-9">
						<h2>Almost done {this.state.fname}!</h2>
						<h3>Choose an available time slot to book your {this.state.scan && this.state.scan.time} appointment.</h3>
						{err && <p>Oops! {err.toString()}</p>}
					</div>
				</div>
				<div className="w-row">
					<div className="w-col w-col-2">
						<div className="timeslotcolumn">
							{offset > 0 && (
								<a href="#"
								   onClick={() => this.setState({offset: offset - 1})}
								   className="buttontimeslot w-button">
									Later Dates &gt;&gt;
								</a>
							)}
						</div>
					</div>
					{times && times.map((date, i) => (
						(i >= offset && (i - offset) < take) && (
							<div key={JSON.stringify(date || i)} className="w-col w-col-2">
								{this.renderSlotAvailabilityDate(date)}
							</div>
						)
					))}
					{offset + take < total && (
						<div className="w-col w-col-2">
							<div className="timeslotcolumn">
								<a href="#"
								   onClick={() => this.setState({offset: offset + 1})}
								   className="buttontimeslot w-button">
									Later Dates &gt;&gt;
								</a>
							</div>
						</div>
					)}
				</div>
			</section>
			</IndexLayout>
		);
	}

	private pickTime(timeSlot: SlotAvailabilityTime) {
		if (timeSlot.isAvailable) {
			sessionStorage.setItem('timeSlot', JSON.stringify(timeSlot));
			navigate('/addl-info');
		}
	}
}

export default PickTimePage;
